var state = require('../../lib/state-machine');
var BloomElement = require('../../lib/bloom-element').BloomElement;
var color = require('../../lib/color');

var getRgb = color.getRgb;
var fullColor = color.fullColor;
var offset = color.offset;

var ui = require('./ui');

class FractalOfLife {
    /**
     * 
     * @param {*} emerga 
     */
    constructor(emerga) {
        this.emerga = emerga;
        this.inited = false;
        this._branchAngle = 54;
        this._segments = 5;
        this._numIterations = 3;
        this._scaleFactor = 256;
        this._flipNegatives = false;
        this._skew = false;
        this._skewAngle = 0;
        this._offset = 600;
        this._useFabric = false;
        this._usePoints =false;
        this._useLines = true;
        this._pointScale = 16;
        this._variablePointSize = false;
        this._variableLineThickness = false;
        this._lineThickness = 1;
        this._lineBrightness = 50;
        this._twistFactor = 0;
        this._segmentStartIndex = 0;
        this._globalCompositeOperation = 'luminosity';
    }
    get branchAngle() {return this._branchAngle;}
    get segments() {return this._segments;}
    get numIterations() {return this._numIterations;}
    get scaleFactor() {return this._scaleFactor;}
    get flipNegatives() {return this._flipNegatives;} 
    get skew() {return this._skew;}
    get skewAngle() {return this._skewAngle;}

    get offset() {return this._offset;}
    get useFabric() {return this._useFabric;}
    get usePoints() {return this._usePoints;}
    get useLines() {return this._useLines;}
    get pointScale() {return this._pointScale;}
    get variablePointSize() {return this._variablePointSize;}
    get variableLineThickness() {return this._variableLineThickness;}
    get lineThickness() {return this._lineThickness;}
    get lineBrightness() {return this._lineBrightness;}
    get twistFactor() {return this._twistFactor;}
    get segmentStartIndex() {return this._segmentStartIndex;}
    get globalCompositeOperation () {return this._globalCompositeOperation;}

    set branchAngle(v) {this._branchAngle=v;this.settingsUpdated();}
    set segments(v) {this._segments=v;this.settingsUpdated();}
    set numIterations(v) {this._numIterations=v;this.settingsUpdated();}
    set scaleFactor(v) {this._scaleFactor=v;this.settingsUpdated();}
    set flipNegatives(v) {this._flipNegatives=v;this.settingsUpdated();}
    set skew(v) {this._skew=v;this.settingsUpdated();}
    set skewAngle(v) {this._skewAngle=v;this.settingsUpdated();}
    set offset(v) {this._offset=v;this.settingsUpdated();}
    set useFabric(v) {this._useFabric=v;this.settingsUpdated();}
    set usePoints(v) {this._usePoints=v;this.settingsUpdated();}
    set useLines(v) {this._useLines=v;this.settingsUpdated();}
    set pointScale(v) {this._pointScale=v;this.settingsUpdated();}
    set variablePointSize(v) {this._variablePointSize=v;this.settingsUpdated();}
    set variableLineThickness(v) {this._variableLineThickness=v;this.settingsUpdated();}
    set lineThickness(v) {this._lineThickness=v;this.settingsUpdated();}
    set lineBrightness(v) {this._lineBrightness=v;this.settingsUpdated();}
    set twistFactor(v) {this._twistFactor=v;this.settingsUpdated();}
    set segmentStartIndex(v) {this._segmentStartIndex=v;this.settingsUpdated();}
    set globalCompositeOperation (v) {this._globalCompositeOperation=v;this.settingsUpdated();}

    /**
     * 
     */
    initCanvas() {
        if(!this.useFabric) this.emerga.initCanvas('main');
        this.context = this.useFabric ? null : this.emerga.context;
        this.canvas = this.useFabric ? new fabric.Canvas('main') : this.emerga.context;
    }

    /**
     * 
     */
    settingsUpdated() {
        var self = this;
        if(!this.inited) return;

        // stop the state machine
        var stopStateMachine = (cb) => {
            self.stateMachine.stop(cb);
        };
        var startStateMachine = () => {
            var curCanv = document.getElementById('main');
            var width = curCanv.width, height = curCanv.height;
            if(curCanv) curCanv.parentNode.removeChild(curCanv);
   
            var canv = document.createElement('canvas');
            canv.id = 'main';
            canv.width = width;
            canv.height = height;
            document.body.appendChild(canv);

            self.initCanvas();
            self.initStateMachine();
            self.stateMachine.run(self.numIterations);
        };
        if(this.stateMachine && !this.stateMachine.halted)
            stopStateMachine(startStateMachine);
    }
    /**
     * 
     */
    clearCanvas() {
        this.context.fillRect(0, 0, this.canvas.width, this.canvas.height);
    }
    /**
     * 
     */
    init() {
        var self = this;
        this.initCanvas();
        ui.initToolbar(this);
        this.initStateMachine();
    }
    /**
     * 
     */
    initStateMachine() {
        var self = this;
        var machine = self.stateMachine = new state.StateMachine();
        machine.builders.add(new state.StateBuilder(machine, null, (el, iter) => {
            if(self.useFabric) {

            } else {
                self.context.clearRect(0,0, self.canvas.width, self.canvas.height);
            }
            var nel = new BloomElement(
                machine, {
                    settings: self, 
                    id: '', 
                    x: 0, 
                    y: 0, 
                    parent: null, 
                    angle: 0,
                    segments: self.segments},
                    BloomElement.bloom);
            machine.elements.add(nel);
        }));
        machine.operators.add(new state.StateOperator(machine, null, (el, iter) => {
            // execute the operator on the element
            // and add resulting elements to list
            return el.execute(iter);
        }));
        machine.postIteration = (machine, _els, iter) => {
            machine.elements.add(_els);
            return iter > self.numIterations;
        };
        machine.renderers.add(new state.StateRenderer(machine, null, (el, iter) => {
            var me = this;
            var strokeWidth = ( iter > 8 ? 1 : 8 / iter );
            self.context.globalCompositeOperation = self.globalCompositeOperation;
            var drawLine = (start, end) => {
                var strokeStyle = fullColor(getRgb(iter), 0.01 * self.lineBrightness);
                if(!self.useFabric) {
                    if (self.useLines) {
                        self.context.moveTo(start[0],start[1]);
                        self.context.lineTo(end[0],end[1]);
                        self.context.strokeStyle = strokeStyle;
                        self.context.strokeWidth = 
                            self.variableLineThickness ? 
                            ( self.lineThickness * strokeWidth ) / 2 : 
                                self.lineThickness;
                    }
                    if(self.usePoints) {
                        var pointSize = 
                            self.variablePointSize ? 
                            self.scaleFactor / (iter * self.pointScale) : self.pointScale;
                        self.context.arc(start[0],start[1], pointSize, 0, 2 * Math.PI, false);
                        self.context.fillStyle = strokeStyle;
                        self.context.fill();
                        self.context.lineWidth = 1;
                        self.context.strokeStyle = strokeStyle;
                    } 
                } else {
                    self.canvas.add(new fabric.Line({
                        x1:start[0], 
                        y1:start[1],    
                        x2:end[0], 
                        y2:end[1], 
                        stroke: strokeStyle,
                        strokeWidth: strokeWidth
                      }));
                }
            };
            if(self.stateMachine.halted) {
                self.clearCanvas();
            }
            var originCoords = offset(el.origin(self.scaleFactor), self.offset);
            self.context.beginPath();
            el.children.forEach((child) => {
                var childCoords = offset(child.origin(self.scaleFactor), self.offset);
                drawLine(originCoords, childCoords);           
                });
            if(!self.useFabric) self.context.stroke();
        }));
    }
    /**
     * 
     */
    run() {
        var div = document.createElement('canvas');
        div.id = 'main';
        div.width = '1200';
        div.height = '1200';
        document.body.appendChild(div);

        this.emerga
            .initStats(0)
            .initGUI('gui', {
                load: JSON,
                preset: 'Fractal of Life'
            });

        ui.initInterface(this);
        this.inited = true;

        this.stateMachine.run(this.numIterations);
    }
}
module.exports = FractalOfLife;